'use client';

import { createPoll, PollOption } from '@/lib/PollService';
import { useState } from 'react';

export default function SubmitPollForm() {
  const [question, setQuestion] = useState('');
  const [options, setOptions] = useState<PollOption[]>([{ id: '1', votes: 0, name: 'potato', isChecked: false}]);
  const [message, setMessage] = useState(''); 

  const setDefaultValues = () => {
    setQuestion('');
    setOptions([]);
  }

  const showMessage = (message: string) => {
    setMessage(message);

    setTimeout(() => {
      setMessage('');
    }, 3000);
  }

  const handleSubmitPoll = async () => {
    if (!question || options.some(opt => !opt.name.trim())) {
      return;
    }

    try {
      // const response = await createPoll({
      //   question,
      //   options: options,
      // });
      // console.log('Created poll:', response);

      // setDefaultValues();

      // showMessage(`Poll with id ${response} created successfully!`)
    } catch (error: any) {
      console.error(error);
      showMessage('Failed to create poll.');
    }
  };

  return (
    <div className="max-w-md mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">{question}</h1>
      <div className="space-y-2">
        {options.map((item, index) => (
          <div>
            <input
              className="p-2 bg-gray-100"
              type='checkbox'
              onChange={(e) => item.isChecked = e.target.checked}/>

            <label>{item.name}</label>
          </div>
        ))}

      </div>

      <br></br>

      <button
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
          onClick={handleSubmitPoll}
        >
          Submit
        </button>

        {message && (
        <div className="mt-4 text-center text-sm font-semibold">
          <p className="text-green-500">{message}</p>
        </div>
      )}
    </div>
  );
}